<template>
  <div class="course-board-note">
    <head-top title="板书分享">
      <!--      <span slot="right" @click="toAdd">-->
      <!--        <van-icon name="plus" size="2.3rem" color="#fff" class="head-right"></van-icon>-->
      <!--      </span>-->
    </head-top>

    <div v-if="boardNoteList && boardNoteList.length> 0" class="flex-wrap-between sub_module">
      <div v-for="(sub, k) in boardNoteList" :key="k" class="image_card van-hairline--surround">
        <r-image width="42vw" height="30vw" :src="sub.imgSrc">
        </r-image>
        <div>{{sub.createTime}}</div>
      </div>
      <span style="width: 42vw;"></span>
    </div>
    <r-lack-data v-else></r-lack-data>

    <!-- 新建分享-->
    <van-popup v-model="editVisible" position="bottom">
      <div class="sub_nav">
        <span>板书分享</span>
      </div>
      <van-field name="uploader" label="上传图片">
        <template #input>
          <van-uploader v-model="uploader" :max-count="1" :before-read="beforeRead" :after-read="afterRead"/>
        </template>
      </van-field>
      <van-button type="info" style="width: 100%;" @click="pictureSubmit">确定</van-button>
    </van-popup>
  </div>
</template>

<script>
import RImage from "@/components/r-image";
import { ModulePicture } from "@/utils/enum";
import { formatDate } from "@/utils/formatData";

export default {
  name: "boardNote",
  components: { RImage },
  data () {
    return {
      groupIndex: 0,
      boardNoteList: [],
      editVisible: false,
      uploader: []
    };
  },
  mounted () {
    this.getBoardNoteList();
  },
  methods: {
    getBoardNoteList () {
      this.boardNoteList = [];
      this.$gatewayHttp.boardNote.getDataList({
        CIID: this.$store.getters.courseID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });
          res.Datas.forEach(item => {
            this.boardNoteList.push({
              title: item["BNTitle"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd hh:mm"),
              imgSrc: this.$gatewayHttp.resourse.picture(item["BNContent"], ModulePicture.BoardNote),
              id: item["BNID"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
      }).catch(() => {
      });
    },
    randomIndex (max) {
      return Math.round(max * Math.random());
    },
    toAdd () {
      this.uploader = [];
      this.editVisible = true;
    },
    // 返回布尔值
    beforeRead (file) {
      if (file.type !== 'image/jpeg') {
        this.$toast('请上传 jpg 格式图片');
        return false;
      }
      return true;
    },
    afterRead (file) {
      file.status = 'uploading';
      file.message = '上传中...';

      setTimeout(() => {
        file.status = 'success';
        file.message = '';
      }, 1000);
    },
    pictureSubmit () {
      console.log(this.uploader);
      const nowDate = new Date();
      const h = nowDate.getHours();
      const m = nowDate.getMinutes();
      const s = nowDate.getSeconds();
      this.boardNoteList.splice(0, 0, {
        id: "",
        imgSrc: this.uploader[0].content,
        createTime: (h > 9 ? h : '0' + h) + ":" + (m > 9 ? m : '0' + m) + ":" + (s > 9 ? s : '0' + s)
      });
      this.editVisible = false;
    }
  },
  watch: {
    "$store.getters.noteFresh" (newVal, oldVal) {
      if (newVal !== oldVal && newVal) {
        this.getBoardNoteList();
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";

.course-board-note {
  min-height: 100%;
  color: #111;
  background-color: #fff;
}
</style>
