<template>
  <div>
    <van-image :fit="fit" :src="src" @click.stop.prevent="preview" :style="{ width: width, height: height }">
      <template v-slot:loading>
        <van-loading type="spinner" size="20"/>
      </template>
      <template v-slot:error>{{ errMsg }}</template>
    </van-image>
  </div>
</template>
<script>
/****
 * 统一图片组件，本组件支持单张图片预览
 * gfy
 * 2019-08-20
 *  */
import { ImagePreview } from "vant";

export default {
  name: "r-image",
  components: {},
  props: {
    // maxWidth: {
    //   type: String,
    //   default: "10rem"
    // },
    // maxHeight: {
    //   type: String,
    //   default: ""
    // },
    width: {
      type: String,
      default: "100px"
    },
    height: {
      type: String,
      default: "100px"
    },
    fit: {
      type: String,
      default: "scale-down"
    },
    src: {
      type: String,
      default: ""
    },
    errMsg: {
      type: String,
      default: "加载失败"
    }
  },
  data () {
    return {
      imagePreview: null
    };
  },
  methods: {
    preview () {
      this.imagePreview = ImagePreview({
        images: [this.src],
        showIndex: false,
        onClose () {
        }
      });
    }
  },
  beforeDestroy () {
    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>
